<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item  :to="{ path: '../dashboard' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    <div class="main-panle">
      <form class="from-reset relative" :model="formInline">
            <div class="from-group">
              <el-input
                v-model="formInline.name"
                placeholder=""
                clearable>
              </el-input>
            </div>
            <div class="from-group from_number">
              <el-input
                v-model="formInline.sn"
                placeholder=""
                clearable>
              </el-input>
            </div>
            <div class="from-group from_size">
              <el-input
                v-model="formInline.stock"
                placeholder=""
                clearable>
              </el-input>
            </div>
            <div class="from-group">
              <el-date-picker
              v-model="formInline.enddate"
              default-time="00:00:00"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="">
            </el-date-picker>
            </div>
            <div class="from-group">
              <el-button type="primary" @click="getList(true)"></el-button>
            </div>
              <div class="from-group">
              <el-button type="primary" @click="add('ruleForm')"></el-button>
            </div>
        </form>
      </div>
      <el-table
        v-loading="loading"
        :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            align='center'
            label="">
            <template slot-scope="scope">
              <img :src="'http://huahong.yongweisoft.cn/'+ scope.row.imgurl" width="80" height="40">
            </template>
          </el-table-column>
          <el-table-column
          align='center'
          prop="company"
          label="">
          </el-table-column>
          <el-table-column
          label=""
          prop="name"
          align='center'>
          </el-table-column>
          <el-table-column
          width="150"
          label=""
          align='center'>
          <template slot-scope="scope">
            <p class="dtail_number" @click='detail(scope.row.id)'>{{scope.row.sn}}</p>
          </template>
          </el-table-column>
          <el-table-column
          align='center'
          prop="price"
          label="">
          </el-table-column>
          <el-table-column
          align='center'
          prop="specifications"
          label="">
          </el-table-column>
          <el-table-column
          align='center'
          prop="stock"
          label="">
          </el-table-column>
          <el-table-column
          align='center'
          prop="enddate"
          label="">
          </el-table-column>
          <el-table-column
          align='center'
          label="">
              <template slot-scope='scope'>
              <span
              @click="changeState(scope.row.id,scope.row.status)"
              :class="scope.row.status == '1'?'tag_success':'tag_danger'"
              >{{scope.row.status == "1"?'':''}}
              </span>
          </template>
          </el-table-column>
          <el-table-column
          align='center'
          width="180"
          label="">
          <template slot-scope="scope">
            <button type="button" class="el-button el-button--default el-button--small" @click="editor(scope.$index,scope.row.id,tableData,'ruleForm')"><span></span></button>
            <button type="button" class="el-button el-button--danger el-button--small" @click="deleted(scope.row.id)"><span></span></button>
          </template>
        </el-table-column>
        </el-table>
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
        <el-dialog
        :title="addTableData.aid==='-1'?'':''" v-model="addTableData.id"
        :visible.sync="dialogVisible"
        width="35%">
        <el-form :model="addTableData" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="name">
            <el-input v-model="addTableData.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="stock">
            <el-input v-model="addTableData.stock" placeholder="" maxlength="5"></el-input>
          </el-form-item>
          <el-form-item label="" prop="company">
            <el-input v-model="addTableData.company" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="specifications">
            <el-input v-model="addTableData.specifications" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="/" prop="price">
            <el-input v-model="addTableData.price" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="sn">
            <el-input v-model="addTableData.sn" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="tag">
            <el-input v-model="addTableData.tag" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="/" prop="period">
            <el-input v-model="addTableData.period" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="enddate">
            <el-date-picker
              v-model="addTableData.enddate"
              type="date"
              placeholder="">
            </el-date-picker>
          </el-form-item>
          <!-- <el-form-item label="">
            <el-upload
              class="avatar-uploader"
              action="https://jsonplaceholder.typicode.com/posts/">
              <img v-if="imageUrl" :src="imageUrl" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item> -->
          <el-form-item label="">
            <el-select v-model='addTableData.sta_goods' style='width: 50%;'>
              <el-option
                label=''
                :value='1'></el-option>
              <el-option
                label=''
                :value='2'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
          <el-select v-model='addTableData.status' style='width: 50%;'>
            <el-option
              :label="''"
              :value='1'></el-option>
            <el-option
              :label="''"
              :value='2'></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
            <el-input
            type="textarea"
            autosize
            placeholder=""
            v-model="addTableData.remarks">
          </el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="cancelForm('ruleForm')"> </el-button>
          <el-button type="primary" @click="submitForm('ruleForm')" :loading='sureLoading'> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogTableVisible" v-model="addTableData.id">
        <ul class="service-detial_ul" :model="addTableData">
          <li class="tc over">
              <div class="fl"></div>
              <div class="fl">{{addTableData.sn}}</div>
          </li>
        </ul>
      </el-dialog>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  data () {
    return {
      // 
      currentPage: 1,
      pageSizes: [5, 10, 20, 50],
      pagesize: 10,
      tableData: [],
      total: 0,
      loading: true,
      sureLoading: false,
      aid: '',
      // 
      formInline: {
        name: '',
        sn: '',
        enddate: '',
        stock: ''
      },
      dialogVisible: false,
      dialogTableVisible: false,
      addTableData: {
        name: '',
        aid: '',
        imgurl: '',
        stock: '',
        company: '',
        specifications: '',
        price: '',
        sn: '',
        period: '',
        enddate: '',
        sta_goods: 1,
        status: 1,
        remarks: '',
        tag: ''
      },
      rules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        stock: [{ required: true, message: '', trigger: 'blur' }],
        company: [{ required: true, message: '', trigger: 'blur' }],
        specifications: [{ required: true, message: '', trigger: 'blur' }],
        price: [{ required: true, message: '', trigger: 'blur' }],
        sn: [{ required: true, message: '', trigger: 'blur' }],
        period: [{ required: true, message: '', trigger: 'blur' }],
        enddate: [{ required: true, message: '', trigger: 'blur' }],
        tag: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    cancelForm (formName) {
      this.dialogVisible = false
      this.$refs[formName].resetFields()
    },
    submitForm (formName) {
      let _this = this
      _this.$refs[formName].validate((valid) => {
        if (valid) {
          this.sureLoading = true
          if (_this.addTableData.aid === '-1') {
            let da = {
              tag: 'product',
              op: 'add',
              data: {
                name: this.addTableData.name,
                stock: this.addTableData.stock,
                company: this.addTableData.company,
                specifications: this.addTableData.specifications,
                price: this.addTableData.price,
                sn: this.addTableData.sn,
                period: this.addTableData.period,
                enddate: this.addTableData.enddate,
                sta_goods: this.addTableData.sta_goods,
                status: this.addTableData.status,
                remarks: this.addTableData.remarks,
                imgurl: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg'
              }
            }
            this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(da), {
              headers: {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(res => {
              let da = res.data
              if (da.err > 0) {
                this.getList()
                this.dialogVisible = false
                this.sureLoading = false
              } else {
                this.sureLoading = false
                _this.$Notice.warning({title: da.message})
              }
            }).catch(function () {
              _this.$Notice.error({title: ''})
            })
          } else {
            let da = {
              tag: 'product',
              op: 'update',
              condition: {
                id: this.addTableData.id
              },
              data: {
                name: this.addTableData.name,
                stock: this.addTableData.stock,
                company: this.addTableData.company,
                specifications: this.addTableData.specifications,
                price: this.addTableData.price,
                sn: this.addTableData.sn,
                period: this.addTableData.period,
                enddate: this.addTableData.enddate,
                sta_goods: this.addTableData.sta_goods,
                status: this.addTableData.status,
                remarks: this.addTableData.remarks,
                imgurl: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg'
              }
            }
            this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(da), {
              headers: {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(res => {
              let da = res.data
              if (da.err > 0) {
                this.getList()
                this.dialogVisible = false
                this.sureLoading = false
              } else {
                this.sureLoading = false
                _this.$Notice.warning({title: da.message})
              }
            }).catch(function () {
              _this.$Notice.error({title: ''})
            })
          }
        } else {
          return false
        }
      })
    },
    add (formName) {
      this.dialogVisible = true
      this.addTableData = {
        name: '',
        aid: '-1',
        imgurl: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg',
        stock: '',
        company: '',
        specifications: '',
        price: '',
        sn: '',
        period: '',
        enddate: '',
        sta_goods: 1,
        status: 1,
        remarks: '',
        tag: ''
      }
      this.$refs[formName].resetFields()
    },
    editor (index, id, list, formName) {
      this.dialogVisible = true
      this.sureLoading = false
      this.addTableData = Object.assign({}, list[index])
      this.$refs[formName].resetFields()
    },
    handleSizeChange (size) {
      this.pagesize = size
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    getList (isSearch) {
      let _this = this
      if (isSearch) {
        this.currentPage = 1
      }
      let d = {
        tag: 'product',
        op: 'getlist',
        condition: {
          'name': this.formInline.name,
          'sn': this.formInline.sn,
          'enddate': this.formInline.enddate,
          'stock': this.formInline.stock
        }
      }
      this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
          this.loading = false
        } else {
          // this.$Notice.warning({title: da.message})
          this.loading = false
        }
      }).catch(function () {
        _this.$Notice.error({title: ''})
      })
    },
    // 
    detail () {
      this.dialogTableVisible = true
    },
    // 
    deleted (id) {
      let _this = this
      _this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let d = {
          tag: 'product',
          op: 'state',
          condition: {
            id: id
          },
          data: {
            status: '0'
          }
        }
        _this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(d), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(res => {
          let da = res.data
          if (da.err > 0) {
            _this.getList()
            _this.$message({
              type: 'success',
              message: '!'
            })
          } else {
            _this.$notify({
              title: '',
              message: da.data.message,
              type: 'warning'
            })
          }
        }).catch(function () {
          _this.$Notice.error({title: ''})
        })
      })
    },
    changeState (id, status) {
      let _this = this
      if (status === 1) {
        status = 2
      } else {
        status = 1
      }
      let d = {
        tag: 'product',
        op: 'state',
        condition: {
          id: id
        },
        data: {
          status: status
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$message({
            type: 'success',
            message: da.message
          })
        } else {
          _this.$notify({
            title: '',
            message: da.message,
            type: 'warning'
          })
        }
      }).catch(function () {
        _this.$Notice.error({title: ''})
      })
    }
  }
}
</script>



